
export default (store) => ({
    path: 'order-summary',
    getComponent(nextState, cb) {
        require.ensure([], (require) => {
            const orderSummary = require('./components/OrderSummaryComponent').default
            cb(null, orderSummary)

        }, 'orderSummary')
    }
})



